import React, { useState, useEffect } from 'react'
import constate from 'constate'
import { IntlProvider } from 'react-intl'
import { ConfigProvider } from 'antd'
import antdEnUS from 'antd/es/locale/en_US'
import antdZhCN from 'antd/es/locale/zh_CN'
import moment from 'moment'
import 'moment/locale/zh-cn'
import { LocalStorage } from '@/utils/storage'
import enUS from './en-US'
import zhCN from './zh-CN'

const locales = {
  'en-US': 'en-US',
  'zh-CN': 'zh-CN',
  _: 'zh-CN',
}

const chooseLocale = locale => locales[locale] || locales._

const antdLocales = {
  'en-US': antdEnUS,
  'zh-CN': antdZhCN,
}

const localeMessages = {
  'en-US': enUS,
  'zh-CN': zhCN,
}

const defaultLocale = navigator.language || navigator.userLanguage
moment.locale(defaultLocale)

const [Provider, useLocale, useSetLocale] = constate(
  ({ defaultLocale }) => {
    const [locale, setLocale] = useState(chooseLocale(defaultLocale))
    return {
      locale,
      setLocale: locale => {
        setLocale(chooseLocale(locale))
      },
    }
  },
  value => value.locale,
  value => value.setLocale,
)

const Comsumer = ({ localStorageKey, children }) => {
  const locale = useLocale()
  moment.locale(locale)

  useEffect(() => {
    LocalStorage.setItem(localStorageKey, locale)
    document.documentElement.lang = locale
  }, [localStorageKey, locale])

  return (
    <IntlProvider locale={locale} messages={localeMessages[locale]}>
      <ConfigProvider locale={antdLocales[locale]}>{children}</ConfigProvider>
    </IntlProvider>
  )
}

export const LocaleProvider = ({ defaultLocale, localStorageKey = 'lang', children }) => (
  <Provider defaultLocale={LocalStorage.getItem(localStorageKey) || defaultLocale}>
    <Comsumer localStorageKey={localStorageKey}>{children}</Comsumer>
  </Provider>
)

export { useLocale, useSetLocale }
