import { init as initApm } from '@elastic/apm-rum'

const apm = initApm({
  serverUrl: process.env.REACT_APP_APM_SERVER_URL,
  serviceName: process.env.REACT_APP_CI_PROJECT_PATH_SLUG || 'development',
  serviceVersion: process.env.REACT_APP_CI_COMMIT_SHA,
  environment: process.env.REACT_APP_ENV,
  active: !!process.env.REACT_APP_CI,
  disableInstrumentations: ['eventtarget'],
})

apm.addLabels({
  group: 'bigdata-fe',
})

apm.observe('transaction:end', tr => {
  if (tr.experience?.longtask) {
    return
  }

  let count = 0
  let sum = 0
  let max = 0

  for (const span of tr.spans) {
    if (span.type.split('.')[0] === 'longtask') {
      const duration = span.duration()
      count++
      sum += duration
      if (duration > max) {
        max = duration
      }
    }
  }

  if (!count) {
    return
  }

  if (!tr.experience) {
    tr.experience = {}
  }

  tr.experience.longtask = {
    count,
    sum,
    max,
  }
})

class ConsoleError extends Error {
  constructor(message, name) {
    super(message)

    this.name = name

    if (/^ {4}at /m.test(message)) {
      this.stack = message
    } else {
      // 去掉一层stack
      this.stack = this.stack.replace(/^ {4}at .*\n/m, '')
    }
  }
}

let inCaptureError = false

const patchConsole = level => {
  const origMethod = console[level]
  const levelErrorName = {
    error: 'ConsoleError',
    warn: 'ConsoleWarning',
  }
  const errorName = levelErrorName[level]

  if (!errorName) {
    return
  }

  console[level] = (...args) => {
    let error = args[0]

    if (!inCaptureError && !/^\[Elastic APM\] /.test(error)) {
      // 避免死循环
      inCaptureError = true

      try {
        if (error instanceof Error) {
          error.message = `${error.name}: ${error.message}`
          error.name = errorName
        } else {
          error = new ConsoleError(args.join(' '), errorName)
        }

        apm.captureError(error)
      } catch (e) {}
    }

    inCaptureError = false
    origMethod.apply(console, args)
  }
}

patchConsole('error')
patchConsole('warn')

window.addEventListener(
  'click',
  () => {
    const target = document.activeElement

    if (!target || target === document.body || target === document.documentElement) {
      return
    }

    const name = target.getAttribute('name')

    let additionalInfo = ''
    if (name) {
      additionalInfo = `["${name}"]`
    }

    const tagName = target.tagName.toLowerCase()

    const tr = apm.startTransaction(`Click - ${tagName}${additionalInfo}`, 'user-interaction', {
      managed: true,
      canReuse: true,
      reuseThreshold: 100,
    })

    if (tr) {
      tr.addContext({ custom: { selector: getSelectorByElement(target) } })
    }
  },
  true,
)

function getSimpleSelectorByElement(element) {
  const { id, className } = element
  let { tagName } = element

  if (!tagName) {
    return null
  }

  tagName = tagName.toLowerCase()

  if (tagName === 'body') {
    return tagName
  }

  if (id) {
    return `#${id}`
  }

  const classSelector = ` ${className}`.replace(/\s+$/, '').replace(/\s+/g, '.')
  const name = element.getAttribute('name')
  const nameSelector = name ? `[name=${name}]` : ''

  return `${tagName}${classSelector}${nameSelector}`
}

function getSelectorByElement(element) {
  try {
    const maxHeight = 5
    const selectors = []

    for (let height = 0; element && height < maxHeight; element = element.parentNode, height++) {
      const simpleSelector = getSimpleSelectorByElement(element)

      if (!simpleSelector) {
        break
      }

      selectors.push(simpleSelector)

      if (simpleSelector === 'body' || simpleSelector[0] === '#') {
        break
      }
    }

    return selectors.reverse().join(' > ')
  } catch (err) {
    return '<unknown>'
  }
}
