import { parse } from 'qs'
import { notification } from 'antd'

// prodConfig单独列出来是为了避免production打包envConfigs和queryConfig
const prodConfig = {
  API_BASE: '//prod.mi.com/api/',
  CAS_ORIGIN: 'https://cas.mioffice.cn',
}

let envConfig // 编译期环境变量REACT_APP_ENV相关的配置
let queryConfig // 运行时URL的query相关的配置

if (process.env.REACT_APP_ENV !== 'production') {
  const dict = {
    API_BASE: {
      production: prodConfig.API_BASE,
      staging: '//staging.mi.com/api/',
      dev01: '//dev01.mi.com/api/',
    },
    CAS_ORIGIN: {
      production: prodConfig.CAS_ORIGIN,
      staging: 'https://casdev.mioffice.cn',
    },
  }

  const { API_BASE, CAS_ORIGIN } = dict

  const envConfigs = {
    production: prodConfig,
    staging: {
      API_BASE: API_BASE.staging,
      CAS_ORIGIN: CAS_ORIGIN.staging,
    },
    review: {
      API_BASE: API_BASE.staging,
      CAS_ORIGIN: CAS_ORIGIN.staging,
    },
    development: {
      API_BASE: API_BASE.staging,
      CAS_ORIGIN: CAS_ORIGIN.staging,
    },
  }
  envConfig = envConfigs[process.env.REACT_APP_ENV]

  const { env, ...query } = parse(window.location.search.slice(1))

  if (env === 'production') {
    notification.error({
      message: '线上环境警告',
      description: '注意：你现在操作的是线上环境',
      duration: null,
    })

    document.documentElement.classList.add('production-env')
  }

  queryConfig = {
    ...envConfigs[env],
  }

  for (const [key, value] of Object.entries(query)) {
    if (key.startsWith('env.')) {
      const name = key.slice(4)
      queryConfig[name] = (dict[name] && dict[name][value]) || value
    }
  }
}

export default {
  ...prodConfig,
  ...envConfig,
  ...queryConfig,
}
