import constate from 'constate'
import { useImmerReducer } from 'use-immer'

const reducers = {
  setUsername: (state, { payload }) => {
    state.username = payload
  },
}

const [GlobalProvider, useUsername, useGlobalDispatch] = constate(
  ({ username = 'Saber' }) => {
    const [state, dispatch] = useImmerReducer(
      (draft, action) => reducers[action.type](draft, action),
      { username },
    )

    return {
      state,
      dispatch,
    }
  },
  value => value.state.username,
  value => value.dispatch,
)

export { GlobalProvider, useUsername, useGlobalDispatch }
