import 'react-app-polyfill/ie9'
import 'react-app-polyfill/stable'
import 'abortcontroller-polyfill/dist/polyfill-patch-fetch'
import '@/utils/elastic-apm'
import { enableAllPlugins } from 'immer'
import React, { StrictMode } from 'react'
import { render } from 'react-dom'
import { BrowserRouter } from 'react-router-dom'
import { LocaleProvider } from '@/locales'
import { GlobalProvider } from '@/models/global'
import 'antd/dist/antd.less'
import './index.less'
import App from './App'
import env from './env'

console.log(env.API_BASE)

enableAllPlugins()

const routerPrefix = `${window.location.pathname.replace(/^\/+/, '/')}${window.location.search}`

window.history.replaceState(null, null, `${routerPrefix}${window.location.hash || '#/'}`)

render(
  <StrictMode>
    <BrowserRouter basename={`${routerPrefix}#`}>
      <LocaleProvider>
        <GlobalProvider>
          <App />
        </GlobalProvider>
      </LocaleProvider>
    </BrowserRouter>
  </StrictMode>,
  document.getElementById('root'),
)
