import React from 'react'
import { Route, Link, Redirect, useLocation } from 'react-router-dom'
import { useIntl } from 'react-intl'
import { ConfigProvider } from 'antd'
import ProLayout, { PageHeaderWrapper, DefaultFooter } from '@ant-design/pro-layout'
import { SmileOutlined } from '@ant-design/icons'
import logo from '@/assets/logo.svg'
import Home from '@/views/Home'
import './BasicLayout.less'

const routeConfig = {
  path: '/',
  routes: [
    {
      icon: <SmileOutlined />,
      name: 'page1',
      path: '/home',
      exact: true,
      component: Home,
    },
    {
      icon: <SmileOutlined />,
      name: 'page2',
      path: '/group',
      routes: [
        {
          icon: <SmileOutlined />,
          name: 'subPage',
          path: '/group/subMenu',
          component: () => '子页面',
        },
      ],
    },
    {
      path: '/',
      exact: true,
      redirect: '/home',
    },
  ],
}

const RouteFromConfig = ({ path, exact, redirect, component: Component, routes = [] }) => (
  <Route path={path} exact={exact}>
    {redirect && <Redirect to={redirect} />}
    {Component && <Component />}

    {routes.map(route => (
      <RouteFromConfig key={route.path} {...route} />
    ))}
  </Route>
)

const menuHeaderRender = (logo, title) => (
  <Link to="/">
    {logo}
    {title}
  </Link>
)

const menuItemRender = (menuItemProps, defaultDom) => {
  if (menuItemProps.isUrl || menuItemProps.children) {
    return defaultDom
  }

  return <Link to={menuItemProps.path}>{defaultDom}</Link>
}

const breadcrumbRender =
  formatMessage =>
  (routers = []) => {
    return [
      {
        path: '/',
        breadcrumbName: formatMessage({ id: 'menu.home' }),
      },
      ...routers,
    ]
  }

const itemRender = (route, params, routes, paths) => {
  const first = routes.indexOf(route) === 0
  return first ? (
    <Link to={paths.join('/')}>{route.breadcrumbName}</Link>
  ) : (
    <span>{route.breadcrumbName}</span>
  )
}

const footerRender = formatMessage => () => {
  return (
    <DefaultFooter
      copyright={`${new Date().getFullYear()} ${formatMessage({ id: 'department' })}`}
      links={false}
    />
  )
}

export default () => {
  const location = useLocation()
  const { formatMessage } = useIntl()

  return (
    <ConfigProvider getPopupContainer={trigger => trigger?.parentNode || document.body}>
      <RouteFromConfig {...routeConfig} />
    </ConfigProvider>
  )
}
